@media (max-width: 767px) {
  .attractions__item:nth-last-child(1):after {
    content: "";
    height: 1px;
    width: 320px;
    background-color: #E5E5E5;
    position: absolute;
    bottom: -24px;
    left: 0;
    margin-left: -20px;
  }
}
.attractions__lable {
  position: relative;
  display: block;
  padding-left: 62px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.attractions__mark {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 46px;
  height: 46px;
  border: none;
  border-radius: 4px;
  background-color: #F2F2F2;
}
.attractions__input[type="checkbox"]:checked + .attractions__mark::after {
  content: "";
  position: absolute;
  mask-image: url("../img/sprite.svg#tick");
  background-repeat: no-repeat;
  background-color: #333333;
  width: 22px;
  height: 16px;
  top: 15px;
  left: 12px;
}
.attractions__item:hover .attractions__mark {
  background-color: #E5E5E5;
}
.attractions__item:active .attractions__input[type="checkbox"]:checked + .attractions__mark::after {
  background-color: rgba(51, 51, 51, 0.3);
}
